import React, {useEffect} from "react";
import Switch from "@material-ui/core/Switch";
import NightsStayIcon from "@material-ui/icons/NightsStay";
import Tooltip from "@material-ui/core/Tooltip";

export default function DarkToggler(props) {

    const [state, setState] = React.useState({
        checkedA: false,
    });
    //debugger
    const localStorageisNightMode = window.localStorage.getItem('isNightMode') ? window.localStorage.getItem('isNightMode') : false;
    let isNightMode = (localStorageisNightMode === 'true');
    //if(typeof isNightMode === undefined) window.localStorage.setItem('isNightMode', false);
    const [stateColor, setStateColor] = React.useState(isNightMode ? 'white' : 'black');
    useEffect(() => {
        setState({...state, checkedA: isNightMode});
    },[isNightMode]);
    useEffect(() => {
        props.darkModeHandler(state);
    }, [state, props]);
    const handleChange = (event) => {
        if (stateColor === 'black') {
            setStateColor('white')
        } else setStateColor('black');
        setState({...state, [event.target.name]: event.target.checked});
        window.localStorage.setItem('isNightMode', event.target.checked);
    };
    return (
        <div
            style={{
                position: "absolute",
                display: "flex",
                alignItems: "center",

                left: "15px",
            }}
        >
            <Tooltip title="Dark Mode">
                <NightsStayIcon style={{fill: stateColor}}/>
            </Tooltip>

            <Switch
                checked={state.checkedA}
                onChange={handleChange}
                name="checkedA"
                inputProps={{"aria-label": "secondary checkbox"}}
            />
        </div>
    );
}
