import {createMuiTheme} from "@material-ui/core/styles";

export const specials = {
    light: {
        aboutBg: "#f9f9f9",
    },
};
export const lightTheme = createMuiTheme({
    name: 'lightTheme',
    cards: {
        bgColor: "#fff",
        headerColor: "initial",
    },

    footerWrap: {
        padding: '30px',

        width: '100%',

        backgroundColor: '#fafafa',
    },
    drawerToolBar: {
        width: '100%',
        minHeight: '100px',
        display: 'flex',
        alignItems: 'center',
        backgroundColor: '#fff',
    },
    copyrights: {
        width: '100%',
        textAlign: 'center',
        fontSize: '1rem',
        marginTop: '30px'
    },
    form: {
        paddingTop: '15px'
    },
    formWrap: {},
    lastArticlesItem: {
        color: '#686868',
    },
    submit: {
        marginTop: '20px',
        color: 'black'
    },

    footerActions: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'end'
    },
    postFooter: {
        width: '100%',
    },
    toolBar: {
        backgroundColor: 'white',
        width: '100%'
    },
    appBar: {
        backgroundColor: 'white',
        position: 'fixed',
        flexDirection: 'row',
        paddingLeft: '20px',
        paddingRight: '20px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between'
    },
    blogPostContent: {
        backgroundColor: 'white',
        paddingRight: '20px',
        paddingTop: '100px',
        flexDirection: 'column',
        width: '70%',
        display: 'flex',
        position: 'absolute',
        justifyContent: 'start',
        alignItems: 'start',
    },
    blog: {
        backgroundColor: 'white',
        overflowX: 'hidden',
        margin: '0',
        fontSize: '1.3rem',
        fontFamily: "OpenSansHebrew",
        color: 'rgba(0,0,0,.8)',
        direction: 'rtl',
        textAlign: 'right',
    },
    card: {
        height: '100%',
        backgroundColor: '#fff',
    },
    postToolBar: {
        display: 'flex',
        width: '100%'
    },
    top: {
        width: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between'
    },
    regNews: {
        color: 'black',
    },
    postHeader: {
        display: 'flex',
        width: '100%'
    },
    arrowColor: {
        fill: 'black',
        cursor: 'pointer'
    },
    drawerText: {
        color: 'black',
    },
    menuButton: {
        color: 'black',
        fill: 'black',
    },
    blogTitle: {
        fontWeight: 800,
        paddingBottom: '20px',
    },
    bar: {
        color: 'black',
        display: 'flex',
    },
    barItem: {
        color: 'black',
        paddingLeft: '15px'
    },
    aboutBg: "#f9f9f9",
    palette: {
        type: "light",
        primary: {
            main: "#00adff",
            contrastText: "#fff", //button text white instead of black
        },
        background: {
            //3b457d
            default: "#ffffff",
        },
    },

    overrides: {
        MuiButton: {
            root: {
                textTransform: "none",
            },
            label: {
                color: "#000",
            },
        },
    },
});

